@import url(https://fonts.googleapis.com/css2?family=Open+Sans:wght@300;400;500;600;700;800&family=Poppins:wght@300;400&display=swap);
:root{--navbar: rgb(131, 91, 42);--dropdown: rgb(58, 56, 54);--background: rgb(248, 247, 244);--navFontColor: rgb(248, 247, 244);--linkFontColor: rgb(248, 247, 244)}*{margin:0;padding:0;box-sizing:border-box;text-decoration:none;list-style:none}body{background-color:rgb(248, 247, 244);background-color:var(--background);font-family:'Open Sans', sans-serif;font-family:'Poppins', sans-serif}code{font-family:source-code-pro, Menlo, Monaco, Consolas, 'Courier New', monospace}

nav{background-color:#111;background-color:var(--navbar);padding:1rem 1rem;display:flex;justify-content:space-around;align-items:center;position:-webkit-sticky;position:sticky;top:0;width:100%;height:5rem;border-bottom:1px solid var(--welcome)}.logo{text-align:left;width:60%;font-size:1.2rem}.logo a{color:var(--navFontColor)}.menu-icon{display:flex;flex-direction:column;cursor:pointer}.menu-icon i{background-color:var(--navFontColor);width:25px;height:2px;margin:2px;border-radius:2px;z-index:10;transition:all ease 0.5s}.menu-icon .open:nth-child(1){-webkit-transform:rotate(45deg) translateY(9px);transform:rotate(45deg) translateY(9px)}.menu-icon .open:nth-child(2){opacity:0}.menu-icon .open:nth-child(3){-webkit-transform:rotate(-45deg) translateY(-9px);transform:rotate(-45deg) translateY(-9px)}.menu-icon .close:nth-child(1){-webkit-transform:rotate(0) translateY(0);transform:rotate(0) translateY(0)}.menu-icon .close:nth-child(2){opacity:1}.menu-icon .close:nth-child(3){-webkit-transform:rotate(0) translateY(0);transform:rotate(0) translateY(0)}.links{display:none}.nav-links-closed{position:absolute;background-color:var(--dropdown);top:5rem;width:100%;height:0vh;transition:all 0.3s ease;overflow:hidden}.nav-links-closed a{color:var(--linkFontColor)}.nav-links-closed ul{display:flex;flex-direction:column;align-items:center;justify-content:center}.nav-links-closed ul li{margin:3rem 0;font-size:1.2rem;font-weight:500;opacity:0}.nav-links{position:absolute;background-color:var(--dropdown);top:5rem;width:100%;height:100vh;transition:all .5s ease;border-bottom:1px solid var(--welcome)}.nav-links a{color:var(--linkFontColor)}.nav-links ul{display:flex;flex-direction:column;align-items:center;justify-content:center}.nav-links ul li{margin:2rem 0;font-size:1.2rem;font-weight:500;opacity:1;transition:opacity .5s ease 0.1s}@media screen and (min-width: 768px){nav{padding:1rem 1rem}.logo{font-size:1.4rem}.menu-icon{display:none}.nav-links-closed{display:none}.nav-links{display:none}.links{display:flex;width:80%}.links ul{width:100%;display:flex;justify-content:flex-end;align-items:center}.links ul li{width:-webkit-fit-content;width:-moz-fit-content;width:fit-content;padding-left:1rem;font-weight:500}.links a{color:var(--navFontColor)}.links a:hover{color:#251f18}}@media screen and (min-width: 1024px){nav{padding:1rem 2rem}.links ul{text-align:center}.links ul li{padding-left:2rem;font-weight:500;font-size:1.1rem}}

.img-container{background-image:url("/duplex-val-thorens/static/media/val thores3.2287afc9.jpg");background-repeat:no-repeat;background-size:cover;background-position:left;width:100%;height:25rem;display:flex;justify-content:center;align-items:center}.welcome{text-align:center;background-color:var(--dropdown);font-size:1.2rem;padding:3rem 1rem;color:white;font-weight:400}@media screen and (min-width: 1024px){.img-container{height:35rem}.welcome{font-size:1.3rem;padding:3rem 7rem;font-weight:400}}

.description{display:flex;flex-direction:column;justify-content:center;align-items:center}.description h2{font-size:1rem;margin:3rem 3rem;text-align:left;font-weight:400}.description .view{width:100%;height:auto}@media screen and (min-width: 1024px){.view-wrapper{width:100%;display:flex;justify-content:center;align-items:center}.view-wrapper h2{width:50%;margin:1rem 0;padding:0rem 9rem;text-align:left;font-size:1.25rem;font-weight:400}.view-wrapper .view{width:50%;height:auto}.reverse{flex-direction:row-reverse}}

.rates-wrapper{display:flex;flex-direction:column;justify-content:center;align-items:center;margin:1rem 1rem 0rem;color:var(--navFontColor)}.rates-wrapper h3{width:100%;height:100%;text-align:center;font-weight:400;background-color:var(--dropdown);background-color:#534d40;padding:0.5rem;border-top:1px solid black;border-bottom:1px solid black;border-right:1px solid black;border-left:1px solid black}.rates-wrapper .titles{width:100%;text-align:center;display:flex}.rates-wrapper .titles h4{width:50%;font-weight:400;background-color:var(--navbar);background-color:#817158;color:#fffdfd;padding:0.5rem;border-bottom:1px solid black;border-right:1px solid black}.rates-wrapper .titles h4:first-child{border-left:1px solid black}.rates-container{width:100%}.rates-container:last-child{margin-bottom:2rem}.rates{display:flex;justify-content:center;align-items:center;text-align:center;border-left:1px solid black;border-right:1px solid black;border-bottom:1px solid black}.rates p{width:50%;color:black;padding:0.5rem}.rates p:first-child{border-right:1px solid black}

.rates-container{margin-bottom:3rem}.rates-title{text-align:center;padding:4rem 2rem 1rem}.rates-h2{text-align:center;padding:2rem 2rem 3rem;font-size:1.1rem;font-weight:400}

.footer-wrapper{background-color:var(--navbar);padding:0 0}.footer-contacts{display:flex;justify-content:center;align-items:center;background-color:var(--navbar)}.footer-contacts ul{display:flex;flex-direction:column;padding:2rem 0rem 0}.footer-contacts ul li{display:flex;font-size:0.9rem;color:var(--navFontColor);font-weight:500;padding:0.5rem 2rem}.footer-contacts ul li p{padding-left:0.3rem}.footer-container{height:5rem;background-color:var(--navbar);display:flex;justify-content:center;align-items:center;color:var(--navFontColor);font-size:0.8rem;padding:0rem 0;font-weight:300}@media screen and (min-width: 1024px){.footer-contacts ul{display:flex;flex-direction:row;justify-content:center;align-items:center;padding:2rem 0rem 0}.footer-contacts ul li{display:flex;justify-content:center;align-items:center;font-size:1rem;padding:0.5rem 1rem}.footer-container{font-size:0.9rem}}

