.rates-wrapper {
    display: flex;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    //border-top: 1px solid black;
    // border-right: 1px solid black;
    // border-left: 1px solid black;
    margin: 1rem 1rem 0rem;
    color: var(--navFontColor);
    h3 {
        width: 100%;
        height: 100%;
        text-align: center;
        font-weight: 400;
        background-color: var(--dropdown); 
        // background-color: rgb(20, 57, 90);
        background-color: rgb(83, 77, 64);   
        padding: 0.5rem;
        border-top: 1px solid black;
        border-bottom: 1px solid black;
        border-right: 1px solid black;
        border-left: 1px solid black;
    }

    .titles {
        width: 100%;
        text-align: center;
        display: flex;
        h4 {
            width: 50%;
            font-weight: 400;
            background-color: var(--navbar);
            // background-color: rgb(50, 103, 126);
            background-color: rgb(129, 113, 88);
            color: rgb(255, 253, 253);
            padding: 0.5rem;
            border-bottom: 1px solid black;
            border-right: 1px solid black;
            

            &:first-child {
                border-left: 1px solid black;    
            }
        }
    }
}

.rates-container {
    width: 100%;
    &:last-child {
        margin-bottom: 2rem;
    }
}

.rates {
    display: flex;
    justify-content: center;
    align-items: center;
    // width: 100%;
    text-align: center;
    border-left: 1px solid black;
    border-right: 1px solid black;
    border-bottom: 1px solid black;
    p {
        width: 50%;
        color: black;
        padding: 0.5rem;
        
        &:first-child {
            border-right: 1px solid black;
        }
    }
}

